body[controls="classic"] .controlsTitle, body[controls="modern"] .controlsTitle {
  display: none;
}

/* left 0 fix for classic controls */
body[controls="default"], body[controls="modern"] {
 --ctrl-left: 0;
}
/* right 0 fix for modern controls */
body[controls="default"], body[controls="classic"] {
  --ctrl-right: 0;
}

body[controls="classic"] .controlsIcon, body[controls="modern"] .controlsIcon {
  display: none;
}

body[controls="classic"] .controls {
  justify-content: flex-end;
}

body[controls="modern"] .controls {
    flex-direction: column-reverse;
    align-items: flex-start;
    justify-content: flex-end;
}

body[controls="classic"] .controlsButton:nth-child(3) {
  display: none;
}

body[controls="classic"] .controlsButton {
  background: var(--highlight);
  border-radius: var(--border-radius);
  color: var(--text-contrast);
}

body[controls="modern"] .controlsButton {
  background: var(--highlight);
  border-radius: var(--border-radius-round);
  color: var(--text-contrast);
}

body[controls="classic"] .controls, body[controls="modern"] .controls {
  background: none;
}

body[controls="classic"] .web, body[controls="modern"] .web {
  height: 100%;
  top: 0;
}

body[controls="classic"] .controlsButton:last-child {
  margin-right: 10px;
}
